#services {
    max-width: 1200px;
    margin: 0 auto;
    padding: 30px 20px;
}

#services h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    color: var(--first-color);
    text-shadow: 2px 2px 4px rgba(0,0,0,0.1);
    text-align: left;
}

.services-content {
    background: var(--third-color);
    border-radius: 15px;
    padding: 30px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.services-content p {
    font-size: 1.1rem;
    line-height: 1.8;
    color: var(--first-color);
    margin: 0;
}